'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import type { PlatformTwilioConfig } from '@/lib/telephony/types'

// ==========================================
// SUPER ADMIN - PLATFORM TELEPHONY SETTINGS
// Level 1: Master Twilio account configuration
// Platform markup is editable and applies to ALL agencies/sub-accounts
// ==========================================

export default function SuperAdminTelephonySettingsPage() {
  const [activeTab, setActiveTab] = useState<'credentials' | 'pricing' | 'provisioning' | 'compliance' | 'features' | 'analytics'>('credentials')
  const [config, setConfig] = useState<PlatformTwilioConfig | null>(null)
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [validating, setValidating] = useState(false)
  const [message, setMessage] = useState<{ type: 'success' | 'error' | 'info'; text: string } | null>(null)

  useEffect(() => {
    fetchConfig()
  }, [])

  const fetchConfig = async () => {
    setLoading(true)
    try {
      const res = await fetch('/api/v1/platform/telephony/config')
      const data = await res.json()
      if (data.success && data.data) {
        setConfig(data.data)
      } else {
        // Initialize with defaults
        setConfig(getDefaultConfig())
      }
    } catch (err) {
      console.error('Error fetching config:', err)
      setConfig(getDefaultConfig())
    } finally {
      setLoading(false)
    }
  }

  const getDefaultConfig = (): PlatformTwilioConfig => ({
    id: '',
    platformId: null,
    accountSid: '',
    authToken: '',
    apiKeySid: null,
    apiKeySecret: null,
    sendgridApiKey: null,
    sendgridVerified: false,
    usageMarkupPercent: 5,  // Default 5% platform markup
    promoMarkupPercent: null,
    promoMarkupStartDate: null,
    promoMarkupEndDate: null,
    promoMarkupDescription: null,
    enableSubaccountBilling: true,
    billingCurrency: 'USD',
    autoProvisionEnabled: true,
    autoProvisionPhoneNumber: false,
    defaultPhoneNumberType: 'local',
    defaultPhoneNumberCountry: 'US',
    defaultPhoneNumberAreaCode: null,
    webhookBaseUrl: null,
    webhookSecretKey: null,
    smsEnabled: true,
    voiceEnabled: true,
    faxEnabled: false,
    mmsEnabled: true,
    brandRegistrationEnabled: true,
    defaultBrandType: 'STANDARD',
    require10DLCForSms: true,
    smsBounceWarningThreshold: 5,
    smsBounceSuspendThreshold: 10,
    smsSpamReportThreshold: 0.1,
    callAbandonRateWarning: 3,
    dailySmsLimitDefault: 1000,
    dailyCallLimitDefault: 500,
    smsPerSecondLimit: 10,
    callsPerSecondLimit: 5,
    isActive: true,
    lastValidatedAt: null,
    validationError: null,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
  })

  const saveConfig = async () => {
    if (!config) return

    setSaving(true)
    setMessage(null)

    try {
      const method = config.id ? 'PUT' : 'POST'
      const res = await fetch('/api/v1/platform/telephony/config', {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(config)
      })

      const data = await res.json()
      if (data.success) {
        setConfig(data.data)
        setMessage({ type: 'success', text: 'Configuration saved successfully!' })
      } else {
        setMessage({ type: 'error', text: data.error || 'Failed to save configuration' })
      }
    } catch (err) {
      setMessage({ type: 'error', text: 'Network error' })
    } finally {
      setSaving(false)
    }
  }

  const validateCredentials = async () => {
    if (!config?.accountSid || !config?.authToken || config.authToken === '********') {
      setMessage({ type: 'error', text: 'Please enter valid Account SID and Auth Token' })
      return
    }

    setValidating(true)
    setMessage({ type: 'info', text: 'Validating Twilio credentials...' })

    try {
      await saveConfig()
    } finally {
      setValidating(false)
    }
  }

  if (loading) {
    return (
      <div className="p-6 max-w-6xl mx-auto">
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-purple-600"></div>
        </div>
      </div>
    )
  }

  return (
    <div className="p-6 max-w-6xl mx-auto">
      {/* Header */}
      <div className="flex justify-between items-center mb-6">
        <div>
          <div className="flex items-center gap-3">
            <Link href="/super-admin/settings" className="text-gray-400 hover:text-gray-600">
              <i className="fas fa-arrow-left"></i>
            </Link>
            <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
              <i className="fas fa-phone-volume text-purple-600"></i>
              Platform Telephony Settings
            </h1>
            <span className="px-2 py-1 bg-purple-100 text-purple-700 text-xs font-medium rounded">
              <i className="fas fa-crown mr-1"></i>
              Super Admin
            </span>
          </div>
          <p className="text-gray-500 text-sm mt-1 ml-8">
            Configure master Twilio account, platform-wide pricing, and global settings
          </p>
        </div>
        <div className="flex gap-3">
          {activeTab === 'credentials' && (
            <button
              onClick={validateCredentials}
              disabled={validating}
              className="px-4 py-2 border border-purple-600 text-purple-600 rounded-lg hover:bg-purple-50 disabled:opacity-50 flex items-center gap-2"
            >
              {validating ? (
                <>
                  <div className="w-4 h-4 border-2 border-purple-600/30 border-t-purple-600 rounded-full animate-spin"></div>
                  Validating...
                </>
              ) : (
                <>
                  <i className="fas fa-check-circle"></i>
                  Validate
                </>
              )}
            </button>
          )}
          <button
            onClick={saveConfig}
            disabled={saving}
            className="px-4 py-2 bg-purple-600 text-white rounded-lg hover:bg-purple-700 disabled:opacity-50 flex items-center gap-2"
          >
            {saving ? (
              <>
                <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin"></div>
                Saving...
              </>
            ) : (
              <>
                <i className="fas fa-save"></i>
                Save Settings
              </>
            )}
          </button>
        </div>
      </div>

      {/* Message */}
      {message && (
        <div className={`mb-6 p-4 rounded-lg flex items-center gap-3 ${
          message.type === 'success' ? 'bg-green-50 border border-green-200 text-green-700' : 
          message.type === 'error' ? 'bg-red-50 border border-red-200 text-red-700' :
          'bg-blue-50 border border-blue-200 text-blue-700'
        }`}>
          <i className={`fas ${
            message.type === 'success' ? 'fa-check-circle' : 
            message.type === 'error' ? 'fa-exclamation-circle' :
            'fa-info-circle'
          }`}></i>
          {message.text}
        </div>
      )}

      {/* 3-Tier Architecture Overview */}
      <div className="mb-6 p-4 bg-gradient-to-r from-purple-50 to-indigo-50 border border-purple-200 rounded-xl">
        <div className="flex items-start gap-4">
          <div className="w-12 h-12 bg-purple-100 rounded-lg flex items-center justify-center flex-shrink-0">
            <i className="fas fa-sitemap text-purple-600 text-xl"></i>
          </div>
          <div className="flex-1">
            <h3 className="font-semibold text-purple-900 mb-2">3-Tier Pricing Architecture</h3>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 text-sm">
              <div className="bg-white/50 p-3 rounded-lg">
                <div className="flex items-center gap-2 mb-1">
                  <span className="w-6 h-6 bg-purple-600 text-white rounded-full flex items-center justify-center text-xs font-bold">1</span>
                  <span className="font-medium text-purple-800">Platform (You)</span>
                </div>
                <p className="text-purple-600 text-xs">Set base markup applied to all. Currently: <strong>{config?.usageMarkupPercent || 5}%</strong></p>
              </div>
              <div className="bg-white/50 p-3 rounded-lg">
                <div className="flex items-center gap-2 mb-1">
                  <span className="w-6 h-6 bg-indigo-600 text-white rounded-full flex items-center justify-center text-xs font-bold">2</span>
                  <span className="font-medium text-indigo-800">Agency (Enterprise Only)</span>
                </div>
                <p className="text-indigo-600 text-xs">Agencies with Enterprise plan can add their own markup on top of platform markup</p>
              </div>
              <div className="bg-white/50 p-3 rounded-lg">
                <div className="flex items-center gap-2 mb-1">
                  <span className="w-6 h-6 bg-blue-600 text-white rounded-full flex items-center justify-center text-xs font-bold">3</span>
                  <span className="font-medium text-blue-800">Sub-Account</span>
                </div>
                <p className="text-blue-600 text-xs">End businesses pay Twilio + Platform markup + Agency markup (if applicable)</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Status Card */}
      {config && (
        <div className={`mb-6 p-4 rounded-xl border ${
          config.isActive && config.lastValidatedAt
            ? 'bg-green-50 border-green-200'
            : 'bg-yellow-50 border-yellow-200'
        }`}>
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-3">
              <div className={`w-10 h-10 rounded-full flex items-center justify-center ${
                config.isActive && config.lastValidatedAt ? 'bg-green-100' : 'bg-yellow-100'
              }`}>
                <i className={`fas ${config.isActive && config.lastValidatedAt ? 'fa-check text-green-600' : 'fa-exclamation text-yellow-600'}`}></i>
              </div>
              <div>
                <p className={`font-medium ${config.isActive && config.lastValidatedAt ? 'text-green-800' : 'text-yellow-800'}`}>
                  {config.isActive && config.lastValidatedAt ? 'Twilio Connected' : 'Setup Required'}
                </p>
                <p className={`text-sm ${config.isActive && config.lastValidatedAt ? 'text-green-600' : 'text-yellow-600'}`}>
                  {config.lastValidatedAt
                    ? `Last validated: ${new Date(config.lastValidatedAt).toLocaleString()}`
                    : 'Enter your Twilio credentials to enable telephony'}
                </p>
              </div>
            </div>
            {config.accountSid && (
              <div className="text-right">
                <p className="text-sm text-gray-500">Account SID</p>
                <code className="text-sm font-mono">{config.accountSid.slice(0, 8)}...{config.accountSid.slice(-4)}</code>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Tabs */}
      <div className="flex gap-1 mb-6 bg-gray-100 p-1 rounded-lg overflow-x-auto">
        {[
          { id: 'credentials', label: 'Twilio Credentials', icon: 'fas fa-key' },
          { id: 'pricing', label: 'Platform Pricing', icon: 'fas fa-dollar-sign' },
          { id: 'provisioning', label: 'Auto-Provisioning', icon: 'fas fa-magic' },
          { id: 'compliance', label: 'Compliance', icon: 'fas fa-shield-alt' },
          { id: 'features', label: 'Features', icon: 'fas fa-toggle-on' },
          { id: 'analytics', label: 'Analytics', icon: 'fas fa-chart-line' }
        ].map(tab => (
          <button
            key={tab.id}
            onClick={() => setActiveTab(tab.id as typeof activeTab)}
            className={`px-4 py-2 rounded-md flex items-center gap-2 whitespace-nowrap transition-colors ${
              activeTab === tab.id
                ? 'bg-white text-purple-600 shadow-sm'
                : 'text-gray-600 hover:text-gray-900'
            }`}
          >
            <i className={tab.icon}></i>
            {tab.label}
          </button>
        ))}
      </div>

      {/* Content */}
      <div className="bg-white rounded-xl shadow-sm border">
        {activeTab === 'credentials' && config && (
          <CredentialsTab config={config} onChange={setConfig} />
        )}
        {activeTab === 'pricing' && config && (
          <PricingTab config={config} onChange={setConfig} />
        )}
        {activeTab === 'provisioning' && config && (
          <ProvisioningTab config={config} onChange={setConfig} />
        )}
        {activeTab === 'compliance' && config && (
          <ComplianceTab config={config} onChange={setConfig} />
        )}
        {activeTab === 'features' && config && (
          <FeaturesTab config={config} onChange={setConfig} />
        )}
        {activeTab === 'analytics' && config && (
          <AnalyticsTab />
        )}
      </div>
    </div>
  )
}

// ==========================================
// CREDENTIALS TAB
// ==========================================

function CredentialsTab({
  config,
  onChange
}: {
  config: PlatformTwilioConfig
  onChange: (config: PlatformTwilioConfig) => void
}) {
  const [showAuthToken, setShowAuthToken] = useState(false)
  const [showSendgridKey, setShowSendgridKey] = useState(false)

  return (
    <div className="p-6 space-y-6">
      {/* Twilio Credentials */}
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
          <span className="text-2xl">ðŸ”µ</span>
          Twilio Master Account
        </h3>
        <div className="bg-purple-50 border border-purple-200 rounded-lg p-4 mb-4">
          <div className="flex items-start gap-3">
            <i className="fas fa-crown text-purple-600 mt-1"></i>
            <div>
              <p className="font-medium text-purple-800">Platform Master Account</p>
              <p className="text-sm text-purple-700">
                This is the master Twilio account for your entire platform. All agency and sub-account usage 
                will be billed through this account. You control the platform markup applied to all clients.
              </p>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Account SID <span className="text-red-500">*</span>
            </label>
            <input
              type="text"
              value={config.accountSid || ''}
              onChange={(e) => onChange({ ...config, accountSid: e.target.value })}
              className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-purple-500"
              placeholder="ACxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx"
            />
            <p className="text-xs text-gray-500 mt-1">Found in your Twilio Console</p>
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Auth Token <span className="text-red-500">*</span>
            </label>
            <div className="relative">
              <input
                type={showAuthToken ? 'text' : 'password'}
                value={config.authToken || ''}
                onChange={(e) => onChange({ ...config, authToken: e.target.value })}
                className="w-full px-4 py-2 pr-12 border rounded-lg focus:ring-2 focus:ring-purple-500"
                placeholder="********************************"
              />
              <button
                type="button"
                onClick={() => setShowAuthToken(!showAuthToken)}
                className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600"
              >
                <i className={`fas ${showAuthToken ? 'fa-eye-slash' : 'fa-eye'}`}></i>
              </button>
            </div>
          </div>
        </div>

        {/* API Key (Optional) */}
        <div className="mt-4 p-4 bg-gray-50 rounded-lg">
          <p className="text-sm font-medium text-gray-700 mb-2">
            <i className="fas fa-key mr-2"></i>
            API Key (Optional, Recommended for Production)
          </p>
          <p className="text-xs text-gray-500 mb-3">
            Using an API Key instead of Auth Token provides better security and the ability to revoke access.
          </p>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">API Key SID</label>
              <input
                type="text"
                value={config.apiKeySid || ''}
                onChange={(e) => onChange({ ...config, apiKeySid: e.target.value })}
                className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-purple-500"
                placeholder="SKxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">API Key Secret</label>
              <input
                type="password"
                value={config.apiKeySecret || ''}
                onChange={(e) => onChange({ ...config, apiKeySecret: e.target.value })}
                className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-purple-500"
                placeholder="********************************"
              />
            </div>
          </div>
        </div>
      </div>

      {/* SendGrid Credentials */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
          <span className="text-2xl">ðŸ“§</span>
          SendGrid (Email)
        </h3>
        <p className="text-sm text-gray-500 mb-4">
          Optional: Configure SendGrid for email services (transactional emails, email campaigns).
        </p>
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">SendGrid API Key</label>
          <div className="relative max-w-md">
            <input
              type={showSendgridKey ? 'text' : 'password'}
              value={config.sendgridApiKey || ''}
              onChange={(e) => onChange({ ...config, sendgridApiKey: e.target.value })}
              className="w-full px-4 py-2 pr-12 border rounded-lg focus:ring-2 focus:ring-purple-500"
              placeholder="SG.xxxxxxxxxxxxxxxxxxxxxxxx"
            />
            <button
              type="button"
              onClick={() => setShowSendgridKey(!showSendgridKey)}
              className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600"
            >
              <i className={`fas ${showSendgridKey ? 'fa-eye-slash' : 'fa-eye'}`}></i>
            </button>
          </div>
        </div>
      </div>

      {/* Webhook Configuration */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
          <i className="fas fa-broadcast-tower text-purple-600"></i>
          Webhook Configuration
        </h3>
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">Webhook Base URL</label>
          <input
            type="url"
            value={config.webhookBaseUrl || ''}
            onChange={(e) => onChange({ ...config, webhookBaseUrl: e.target.value })}
            className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-purple-500"
            placeholder="https://your-domain.com"
          />
          <p className="text-xs text-gray-500 mt-1">
            Base URL for Twilio webhooks. Leave empty to use the default application URL.
          </p>
        </div>
      </div>
    </div>
  )
}

// ==========================================
// PRICING TAB - PLATFORM MARKUP SETTINGS
// ==========================================

function PricingTab({
  config,
  onChange
}: {
  config: PlatformTwilioConfig
  onChange: (config: PlatformTwilioConfig) => void
}) {
  const [showPromoSettings, setShowPromoSettings] = useState(!!config.promoMarkupPercent)

  // Calculate example prices
  const twilioSmsPrice = 0.0075
  const twilioCallPrice = 0.014 // per minute
  const twilioPhoneMonthly = 1.00
  
  const platformMarkup = config.usageMarkupPercent / 100
  const agencyMarkupExample = 0.10 // 10% example agency markup
  
  // Platform only price (for non-Enterprise agencies)
  const platformSmsPrice = twilioSmsPrice * (1 + platformMarkup)
  const platformCallPrice = twilioCallPrice * (1 + platformMarkup)
  const platformPhoneMonthly = twilioPhoneMonthly * (1 + platformMarkup)
  
  // With agency markup (for Enterprise agencies)
  const totalMarkup = platformMarkup + agencyMarkupExample
  const enterpriseSmsPrice = twilioSmsPrice * (1 + totalMarkup)
  const enterpriseCallPrice = twilioCallPrice * (1 + totalMarkup)
  const enterprisePhoneMonthly = twilioPhoneMonthly * (1 + totalMarkup)

  // Check if promo is active
  const now = new Date()
  const promoStartDate = config.promoMarkupStartDate ? new Date(config.promoMarkupStartDate) : null
  const promoEndDate = config.promoMarkupEndDate ? new Date(config.promoMarkupEndDate) : null
  const isPromoActive = config.promoMarkupPercent !== null && 
    promoStartDate && promoEndDate && 
    now >= promoStartDate && now <= promoEndDate

  return (
    <div className="p-6 space-y-6">
      {/* Platform Markup */}
      <div>
        <h3 className="text-lg font-semibold text-gray-900 mb-4 flex items-center gap-2">
          <i className="fas fa-percentage text-purple-600"></i>
          Platform Markup
          {isPromoActive && (
            <span className="px-2 py-1 bg-orange-100 text-orange-700 text-xs font-medium rounded-full">
              <i className="fas fa-tag mr-1"></i>
              Promo Active
            </span>
          )}
        </h3>
        
        <div className="bg-gradient-to-r from-green-50 to-emerald-50 border border-green-200 rounded-lg p-4 mb-4">
          <div className="flex items-start gap-3">
            <i className="fas fa-money-bill-wave text-green-600 mt-1 text-xl"></i>
            <div>
              <p className="font-medium text-green-800">Your Platform Revenue</p>
              <p className="text-sm text-green-700">
                This markup is applied to ALL Twilio charges across the entire platform. 
                It is your profit margin on every SMS, call, and phone number.
                Agencies with Enterprise plans can add additional markup on top of this.
              </p>
            </div>
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Markup Input */}
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-1">
                Platform Markup Percentage
              </label>
              <div className="relative">
                <input
                  type="number"
                  value={config.usageMarkupPercent}
                  onChange={(e) => onChange({ ...config, usageMarkupPercent: parseFloat(e.target.value) || 0 })}
                  className="w-full px-4 py-3 pr-12 border-2 border-purple-200 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-purple-500 text-xl font-semibold"
                  min="0"
                  max="100"
                  step="0.5"
                />
                <span className="absolute right-4 top-1/2 -translate-y-1/2 text-gray-400 text-xl">%</span>
              </div>
              <p className="text-xs text-gray-500 mt-1">
                Default: 5%. Applied to all Twilio charges across all agencies and sub-accounts.
              </p>
            </div>

            {/* Quick Presets */}
            <div>
              <label className="block text-xs font-medium text-gray-500 mb-2">Quick Presets</label>
              <div className="flex flex-wrap gap-2">
                {[3, 5, 7.5, 10, 15, 20].map(percent => (
                  <button
                    key={percent}
                    onClick={() => onChange({ ...config, usageMarkupPercent: percent })}
                    className={`px-3 py-1.5 rounded-full text-sm font-medium transition-colors ${
                      config.usageMarkupPercent === percent
                        ? 'bg-purple-600 text-white'
                        : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                    }`}
                  >
                    {percent}%
                  </button>
                ))}
              </div>
            </div>
          </div>

          {/* Price Calculator */}
          <div className="bg-gray-50 p-4 rounded-lg">
            <p className="text-sm font-medium text-gray-700 mb-3 flex items-center gap-2">
              <i className="fas fa-calculator text-purple-600"></i>
              Price Calculator
            </p>
            
            {/* Standard Agencies */}
            <div className="mb-4">
              <p className="text-xs font-semibold text-gray-500 uppercase mb-2">Standard/Professional Agencies</p>
              <table className="w-full text-sm">
                <thead>
                  <tr className="text-left text-xs text-gray-500">
                    <th className="pb-1">Item</th>
                    <th className="pb-1 text-right">Twilio</th>
                    <th className="pb-1 text-right">Client Pays</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td className="py-1">SMS</td>
                    <td className="py-1 text-right text-gray-500">${twilioSmsPrice.toFixed(4)}</td>
                    <td className="py-1 text-right font-medium text-green-600">${platformSmsPrice.toFixed(4)}</td>
                  </tr>
                  <tr>
                    <td className="py-1">Call (per min)</td>
                    <td className="py-1 text-right text-gray-500">${twilioCallPrice.toFixed(4)}</td>
                    <td className="py-1 text-right font-medium text-green-600">${platformCallPrice.toFixed(4)}</td>
                  </tr>
                  <tr>
                    <td className="py-1">Phone Number</td>
                    <td className="py-1 text-right text-gray-500">${twilioPhoneMonthly.toFixed(2)}</td>
                    <td className="py-1 text-right font-medium text-green-600">${platformPhoneMonthly.toFixed(2)}</td>
                  </tr>
                </tbody>
              </table>
            </div>

            {/* Enterprise Agencies (with agency markup) */}
            <div className="border-t pt-3">
              <p className="text-xs font-semibold text-gray-500 uppercase mb-2">
                Enterprise Agencies <span className="text-indigo-600">(+10% agency markup example)</span>
              </p>
              <table className="w-full text-sm">
                <thead>
                  <tr className="text-left text-xs text-gray-500">
                    <th className="pb-1">Item</th>
                    <th className="pb-1 text-right">Twilio</th>
                    <th className="pb-1 text-right">Sub-Acct Pays</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td className="py-1">SMS</td>
                    <td className="py-1 text-right text-gray-500">${twilioSmsPrice.toFixed(4)}</td>
                    <td className="py-1 text-right font-medium text-indigo-600">${enterpriseSmsPrice.toFixed(4)}</td>
                  </tr>
                  <tr>
                    <td className="py-1">Call (per min)</td>
                    <td className="py-1 text-right text-gray-500">${twilioCallPrice.toFixed(4)}</td>
                    <td className="py-1 text-right font-medium text-indigo-600">${enterpriseCallPrice.toFixed(4)}</td>
                  </tr>
                  <tr>
                    <td className="py-1">Phone Number</td>
                    <td className="py-1 text-right text-gray-500">${twilioPhoneMonthly.toFixed(2)}</td>
                    <td className="py-1 text-right font-medium text-indigo-600">${enterprisePhoneMonthly.toFixed(2)}</td>
                  </tr>
                </tbody>
              </table>
              <p className="text-xs text-gray-400 mt-2">
                Total markup: {config.usageMarkupPercent}% (platform) + 10% (agency) = {config.usageMarkupPercent + 10}%
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Promotional Pricing */}
      <div className="border-t pt-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-gray-900 flex items-center gap-2">
            <i className="fas fa-tag text-orange-500"></i>
            Promotional Pricing
          </h3>
          <button
            onClick={() => {
              setShowPromoSettings(!showPromoSettings)
              if (!showPromoSettings) {
                // Clear promo settings when hiding
                onChange({ 
                  ...config, 
                  promoMarkupPercent: null,
                  promoMarkupStartDate: null,
                  promoMarkupEndDate: null,
                  promoMarkupDescription: null
                })
              }
            }}
            className={`px-4 py-2 rounded-lg text-sm font-medium transition-colors ${
              showPromoSettings
                ? 'bg-orange-100 text-orange-700'
                : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
            }`}
          >
            {showPromoSettings ? (
              <>
                <i className="fas fa-times mr-2"></i>
                Cancel Promo
              </>
            ) : (
              <>
                <i className="fas fa-plus mr-2"></i>
                Create Promo
              </>
            )}
          </button>
        </div>

        {showPromoSettings && (
          <div className="bg-orange-50 border border-orange-200 rounded-lg p-4">
            <p className="text-sm text-orange-700 mb-4">
              <i className="fas fa-info-circle mr-2"></i>
              During the promotional period, this markup will override the standard platform markup.
            </p>
            
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Promo Markup %
                </label>
                <div className="relative">
                  <input
                    type="number"
                    value={config.promoMarkupPercent || ''}
                    onChange={(e) => onChange({ 
                      ...config, 
                      promoMarkupPercent: e.target.value ? parseFloat(e.target.value) : null 
                    })}
                    className="w-full px-4 py-2 pr-8 border rounded-lg focus:ring-2 focus:ring-orange-500"
                    min="0"
                    max="100"
                    step="0.5"
                    placeholder="e.g., 3"
                  />
                  <span className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400">%</span>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Start Date
                </label>
                <input
                  type="datetime-local"
                  value={config.promoMarkupStartDate?.slice(0, 16) || ''}
                  onChange={(e) => onChange({ 
                    ...config, 
                    promoMarkupStartDate: e.target.value ? new Date(e.target.value).toISOString() : null 
                  })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  End Date
                </label>
                <input
                  type="datetime-local"
                  value={config.promoMarkupEndDate?.slice(0, 16) || ''}
                  onChange={(e) => onChange({ 
                    ...config, 
                    promoMarkupEndDate: e.target.value ? new Date(e.target.value).toISOString() : null 
                  })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
                />
              </div>

              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  Description
                </label>
                <input
                  type="text"
                  value={config.promoMarkupDescription || ''}
                  onChange={(e) => onChange({ ...config, promoMarkupDescription: e.target.value || null })}
                  className="w-full px-4 py-2 border rounded-lg focus:ring-2 focus:ring-orange-500"
                  placeholder="e.g., Holiday Special"
                />
              </div>
            </div>

            {config.promoMarkupPercent !== null && config.promoMarkupStartDate && config.promoMarkupEndDate && (
              <div className="mt-4 p-3 bg-white rounded-lg border border-orange-200">
                <p className="text-sm font-medium text-gray-800">
                  <i className="fas fa-calendar-check text-orange-500 mr-2"></i>
                  Promo Summary: <strong>{config.promoMarkupPercent}% markup</strong>
                  {config.promoMarkupDescription && ` - "${config.promoMarkupDescription}"`}
                </p>
                <p className="text-xs text-gray-500 mt-1">
                  Active from {new Date(config.promoMarkupStartDate).toLocaleString()} to {new Date(config.promoMarkupEndDate).toLocaleString()}
                </p>
                {isPromoActive && (
                  <p className="text-xs text-green-600 font-medium mt-1">
                    <i className="fas fa-check-circle mr-1"></i>
                    This promotion is currently active!
                  </p>
                )}
              </div>
            )}
          </div>
        )}
      </div>

      {/* Billing Options */}
      <div className="border-t pt-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Billing Options</h3>
        <div className="space-y-4">
          <label className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
            <div>
              <span className="font-medium text-gray-900">Enable Sub-Account Billing</span>
              <p className="text-sm text-gray-500">Track and bill sub-accounts for their telephony usage</p>
            </div>
            <input
              type="checkbox"
              checked={config.enableSubaccountBilling}
              onChange={(e) => onChange({ ...config, enableSubaccountBilling: e.target.checked })}
              className="w-5 h-5 rounded border-gray-300 text-purple-600"
            />
          </label>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">Billing Currency</label>
            <select
              value={config.billingCurrency}
              onChange={(e) => onChange({ ...config, billingCurrency: e.target.value })}
              className="w-full max-w-xs px-4 py-2 border rounded-lg bg-white"
            >
              <option value="USD">USD - US Dollar</option>
              <option value="CAD">CAD - Canadian Dollar</option>
              <option value="EUR">EUR - Euro</option>
              <option value="GBP">GBP - British Pound</option>
            </select>
          </div>
        </div>
      </div>
    </div>
  )
}

