              <p className="text-sm text-amber-700">
                As of September 2023, all SMS/MMS messages to US numbers must be sent from 10DLC registered campaigns.
                Unregistered messages will be blocked by carriers. Sub-accounts will submit their own registrations
                with AI Virtual Assistant guidance.
              </p>
